
#app{
	width: 100%;
	height:100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
html{
	width: 100%;
	height: 100%;
}

body{
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
}
#guessingCompetition{
	width: 90vw;
	height: 45vw;
	margin: auto;
	background-color: #523f39;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#guessingCompetition .camp{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	height: 100%;
}
#guessingCompetition .left{
	background-color: #8b332f;
}
#guessingCompetition .right{
	background-color: #587593;
}
.gCCamp{
	margin-top: 1.5vw !important;
}

.gCCamp{
	width: 40.5vw;
	height: 13.5vw;
	margin: auto;
	outline: #cfa5b1 2px solid;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.gCShikigami{
	-webkit-box-flex: 1;
	    -ms-flex: 1;
	        flex: 1;
	height: 100%;
	border: 1px solid #000000;
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
}
